@import url(https://fonts.googleapis.com/css?family=Open+Sans|Oswald&display=swap);
body,div,h1,h2,h3,h4,h5,h6{margin:0;padding:0}body{background-color:blue}


.main-component{padding-top:190px}.main-component.home{background-image:url(/boltemplate-react/build/static/media/hero-home.1053dab8.jpg);background-repeat:no-repeat;background-size:cover;height:100vh;background-position:50%}

.primary-hero{background-size:cover;background-position:50%;padding-top:200px}.primary-hero .heading-container{margin-top:35vh;font-family:'Oswald';color:#fff;padding-left:20px}.primary-hero .heading-container h1{font-size:60px;text-transform:capitalize;font-weight:100}.primary-hero .heading-container h2{font-size:24px;text-transform:uppercase;font-weight:100}.primary-hero .separator{width:100%;display:block}

#desktopNav{position:fixed;top:0px;width:100%;z-index:1;display:block;background:linear-gradient(rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%) 0% 0% no-repeat padding-box padding-box transparent;padding:10px;display:flex}#desktopNav .main-logo{width:170px;display:inline-block}@media screen and (max-width: 980px){#desktopNav .main-logo{width:80px}}#desktopNav ul{display:inline-block}#desktopNav ul li{list-style:none;display:inline-block;margin-right:25px;cursor:pointer;font-family:'Oswald';font-size:16px;color:#fff;width:64px}#desktopNav ul .active-tab{border-bottom:1px solid #fff}@media screen and (max-width: 980px){#desktopNav ul{display:none}}#desktopNav .main-menu{vertical-align:top}#desktopNav .search-top{width:30px;height:30px;margin-top:20px;margin-right:15px;margin-left:30px;padding:2px;background:url(/boltemplate-react/build/images//icons/icon_search.svg);background-size:cover;display:inline-block;vertical-align:top}#desktopNav .shop-top{width:30px;height:30px;margin-top:20px;padding:2px;background:url(/boltemplate-react/build/images//icons/icon_cart.svg);background-size:cover;display:inline-block;vertical-align:top}#desktopNav .season-switch{margin-left:auto;margin-right:30px}#desktopNav .season-switch .season-switch-button:nth-child(1){margin-right:10px}#desktopNav #subNav{width:400px;position:absolute;top:-40px;transition:all 0.5s;left:0px;opacity:0;padding:10px;left:220px;background-color:rgba(22,87,103,0.2);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);visibility:hidden}#desktopNav #subNav ul{vertical-align:top}#desktopNav #subNav span{font-family:'Oswald';font-size:16px;line-height:24px;color:#fff;cursor:default}#desktopNav #subNav li{display:block}#desktopNav #subNav li a{text-decoration:none;color:#fff;font-family:'Open Sans';font-size:14px}#desktopNav #subNav .close-icon{width:20px;height:20px;background-image:url("/images/icons/close-icon.svg");position:absolute;top:15px;right:10px;display:block;cursor:pointer;background-size:cover}@media screen and (max-width: 980px){#desktopNav #subNav{width:100%}}#desktopNav .active-menu{visibility:visible !important;visibility:initial !important;background-color:blue;top:80px !important;opacity:1  !important}#desktopNav #mobileMenu{display:none}@media screen and (max-width: 980px){#desktopNav #mobileMenu{display:inline-block;height:20px;width:20px;background-image:url("/images/icons/menu-icon.svg");background-size:cover;vertical-align:top}}

#navMobile{display:none;position:fixed;top:0px;width:100%;z-index:1;display:block;background:linear-gradient(rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%) 0% 0% no-repeat padding-box padding-box transparent;padding:10px;z-index:1}@media screen and (max-width: 980px){#navMobile{display:flex}}#navMobile .main-logo{width:90px;display:inline-block}#navMobile .sub-navigation{background-color:rgba(22,87,103,0.2);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);width:100%;position:absolute;top:-400px;left:0px;transition:all 0.5s;visibility:hidden}#navMobile .mobile-submenu{display:block}#navMobile .mobile-submenu li{list-style:none;display:block}#navMobile .mobile-submenu li span{font-family:'Oswald';font-size:16px;line-height:24px;color:#fff;cursor:default}#navMobile .mobile-submenu li ul{display:none}#navMobile .mobile-submenu li ul li a{text-decoration:none;color:#fff;font-family:'Open Sans';font-size:14px}#navMobile .mobile-submenu li .opened{display:block !important}#navMobile .close-icon{width:20px;height:20px;background-image:url("/images/icons/close-icon.svg");position:absolute;top:15px;right:30px;display:block;cursor:pointer;background-size:cover}#navMobile .submenu-open{top:0px;visibility:visible}#navMobile #mobileMenu{display:inline-block;height:30px;width:30px;background-image:url("/images/icons/menu-icon.svg");background-size:cover;vertical-align:top;margin-left:auto;margin-top:10px;margin-right:20px}#navMobile .search-top{width:20px;height:20px;margin-top:10px;margin-right:15px;margin-left:20px;padding:2px;background-image:url("/images/icons/icon_search.svg");background-size:cover;display:inline-block;vertical-align:top}#navMobile .shop-top{width:20px;height:20px;margin-top:10px;padding:2px;background-image:url("/images/icons/icon_cart.svg");background-size:cover;display:inline-block;vertical-align:top}#navMobile .season-switch{margin-left:auto;margin-right:10px}#navMobile .season-switch .season-switch-button:nth-child(1){margin-right:10px}

